import { defaultProps, defaultOptions } from '../common/component-handler/press-component';

export default {
  name: 'PressPopover',
  options: {
    ...defaultOptions,
    virtualHost: true,
  },
  props: {
    show: {
      type: Boolean,
      default: true,
    },
    placement: {
      type: String,
      default: 'right',
    },
    popperClass: {
      type: String,
      default: '',
    },
    ...defaultProps,
    customStyle: {
      type: String,
      default: '',
    },
  },

  emits: [],
  data() {
    return {
      innerShow: false,
      isEnter: false,

      watchShowTimer: null,
    };
  },
  computed: {
  },
  watch: {
    show: {
      handler(value) {
        if (value) {
          clearTimeout(this.watchShowTimer);

          this.innerShow = value;
          this.isEnter = value;
        } else {
          // remove
          this.isEnter = value;
          clearTimeout(this.watchShowTimer);

          this.watchShowTimer = setTimeout(() => {
            this.innerShow = value;
          }, 300);
        }
      },
      immediate: true,
    },
  },
  methods: {
  },
};

